import { Component, OnInit } from '@angular/core';
import { OrderService } from '../../../../../auth/_services/order.service';
import { QuickSidebarComponent } from '../../../../layouts/quick-sidebar/quick-sidebar.component';
import { SideBarToggleComponent } from '../../../../layouts/side-bar-toggle/side-bar-toggle.component';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { DataService } from '../../../../../_services/data.service';
// import { NotificationsService } from 'angular4-notify';
import { Router, ActivatedRoute } from "@angular/router";
import { ToastrService } from 'ngx-toastr';
import { UserService } from '../../../../../auth/_services/user.service';

@Component({
    selector: 'app-order-list',
    templateUrl: "order-list.component.html",
    styles: []
})
export class OrderListComponent implements OnInit {
    form: FormGroup;
    private formSumitAttempt: boolean;
    constructor(private toastr: ToastrService, private _userService: UserService, private route: ActivatedRoute, private router: Router, private _orderService: OrderService, private _dataService: DataService, private formBuilder: FormBuilder) { }
    public brandData = [{ "pk_order_id": 1, "order_no": "1", "fk_customer_id": 13, "fk_address_id": 25, "fk_payment_type": "cod", "total_amount": 83008, "payment_status": "p", "payment_date": "2018-08-10 13:17:12", "transaction_id": null, "cancelled_status": "n", "deliver_date": "2018-08-11", "created_date": "2018-06-30 18:30:00", "update_date": "2018-08-16 14:56:59", "product_name": "Mi Note 5 Pro", "state": false, "status": "completed", "shipping_status": "d", "refund_status": "n", "slug": "MI-Mi-Note-5-Pro-MI3455632", "customer_name": "Sam" }];
    public searchValue = '';
    public bulkActionData: any;
    public pageTilte = 'Orders List';
    public brandDrpdown: any;
    public dropDownValue = '';
    public selectedStatus = '';
    public dataSaved = false;
    public saveMsg = '';
    brandImgUrl: any;

    public allOrder = 0;
    public completedOrder = 0;
    public processingOrder = 0;
    public on_holdOrder = 0;
    public pendingOrder = 0;
    public cancelledOrder = 0;
    public refundedOrder = 0;


    public orderCheck: any;
    public filterDate: any;
    public filterDataHolder: any;
    public customer_name: string;


    p: number = 1;
    public back_btn: boolean = false;

    ngOnInit() {

        /*user permission*/
        let currentUser = JSON.parse(localStorage.getItem('currentUser'));
        let permission = currentUser.success.permission;
        let where: any;

        if (permission.order_list != 1) {
            this.toastr.error('You have not permission to visit this page.', 'Error Message');
            this.router.navigate(['/']);
        }
        /*user permission*/

        // this.notificationsService.addInfo('Brand Added Successfully');
        this._dataService.setloaderState(false);
        this._dataService.setactiveMenu('order');
        let loggedUserId = JSON.parse(localStorage.getItem('currentUser'));


        this.route.params.subscribe(
            parameter => {
                localStorage.setItem('section', 'order');
                loggedUserId = JSON.parse(localStorage.getItem('currentUser'));
                if (parameter.vendor_id) {
                    //console.log('parameter.vendor_id', parameter.vendor_id);
                    where = { loggedUserId: parameter.vendor_id, loggedUserRole: loggedUserId.success.role, role: '' };
                    this._userService.getById(parameter.vendor_id).subscribe(
                        data => {
                            this.pageTilte = data.success.store_name + '\'s Order';
                            this.back_btn = true;
                        }
                    );
                } else {
                    where = { loggedUserId: loggedUserId.success.id, loggedUserRole: loggedUserId.success.role };
                }
                this._orderService.getAll(where).subscribe(
                    data => {
                        this.brandData = data.success;
                        this.allOrder = this.brandData.length;
                        this.statusOrderData('completed');
                        this.statusOrderData('processing');
                        this.statusOrderData('on_hold');
                        this.statusOrderData('pending');
                        this.statusOrderData('cancelled');
                        this.statusOrderData('refunded');
                    },
                    (err: string) => {
                        console.log('Sorry We have not found any product');

                    }
                );
            }
        );
    }


    statusOrderData(status, filter = false) {
        let order_data = [];
        if (this.filterDataHolder == undefined) {
            this.filterDataHolder = this.brandData;
        } else {
            this.brandData = this.filterDataHolder;
        }

        if (status == 'completed') {
            this.brandData.forEach(obj => {
                if (obj.status == 'completed') {
                    order_data.push(obj);
                }
            });
            this.completedOrder = order_data.length;
            console.log('completed');
        } else if (status == 'processing') {
            this.brandData.forEach(obj => {
                if (obj.status == 'processing') {
                    order_data.push(obj);
                }
            });
            this.processingOrder = order_data.length;
            console.log('processingOrder');

        } else if (status == 'on_hold') {
            this.brandData.forEach(obj => {
                if (obj.status == 'on-hold') {
                    order_data.push(obj);
                }
            });
            this.on_holdOrder = order_data.length;
            console.log('on_holdOrder');

        } else if (status == 'pending') {
            this.brandData.forEach(obj => {
                if (obj.status == 'pending') {
                    order_data.push(obj);
                }
            });
            this.pendingOrder = order_data.length;
            console.log('pendingOrder');

        } else if (status == 'cancelled') {
            this.brandData.forEach(obj => {
                if (obj.status == 'cancelled') {
                    order_data.push(obj);
                }
            });
            this.cancelledOrder = order_data.length;
            console.log('cancelledOrder');

        } else if (status == 'refunded') {
            this.brandData.forEach(obj => {
                if (obj.status == 'refunded') {
                    order_data.push(obj);
                }
            });
            this.refundedOrder = order_data.length;
            console.log('refundedOrder');

        } else {
            console.log('working');
        }

        if (filter == true) {
            this.brandData = order_data;
        }
    }







    isFieldValid(field: string) {
        return (
            (!this.form.get(field).valid && this.form.get(field).touched) ||
            (this.form.get(field).untouched && this.formSumitAttempt)
        );
    }

    displayFieldCss(field: string) {
        return {
            'has-error': this.isFieldValid(field),
            'has-feedback': this.isFieldValid(field)
        };
    }


    handleFileInput(files: FileList) {
        let file = files.item(0);
        var reader = new FileReader();
        reader.onload = (event: any) => {
            this.brandImgUrl = event.target.result;
        }
        reader.readAsDataURL(file);
    }


    hideMsg() {
        setTimeout(function() {
            this.dataSaved = false;
        }.bind(this), 4000);
    }

    reset() {
        if (this.selectedStatus != '') {
            this.form.reset();
            this.selectedStatus = '';
            this.formSumitAttempt = false;
        }
    }



    public orderFilter() {
        let loggedUserId = JSON.parse(localStorage.getItem('currentUser'));

        this._orderService.filter({ search_value: this.searchValue, dropdown_value: this.dropDownValue, loggedUserId: loggedUserId.success.id, loggedUserRole: loggedUserId.success.role }).subscribe(
            data => {
                this.brandData = data.success;
            }
        );
    }

    checkAll(ev, brandData: any) {
        brandData.forEach(x => x.state = ev.target.checked)
    }

    isAllChecked(brandData: any) {
        return brandData.every(_ => _.state);
    }

    filterByCustomer() {
        let order_data = [];
        if (this.customer_name == '') {
            this.brandData = this.filterDataHolder;
        } else {
            if (this.filterDataHolder == undefined) {
                this.filterDataHolder = this.brandData;
            } else {
                this.brandData = this.filterDataHolder;
            }
            this.brandData.forEach(obj => {
                if (obj.customer_name != '' && obj.customer_name != null && obj.customer_name != undefined) {
                    if (obj.customer_name.replace(/<[^>]*>/g, "").toUpperCase().indexOf(this.customer_name.toUpperCase()) > -1) {
                        //console.log(obj);
                        order_data.push(obj);
                    }
                }
            });
            this.brandData = order_data;
        }
    }

    public filterByDate() {
        let order_data = [];

        if (this.filterDate == '') {
            this.brandData = this.filterDataHolder;
        } else {
            if (this.filterDataHolder == undefined) {
                this.filterDataHolder = this.brandData;
            } else {
                this.brandData = this.filterDataHolder;
            }

            this.brandData.forEach(obj => {
                let created_date = obj.payment_date.split(' ');
                //created_date = created_date;
                if (created_date[0] == this.filterDate) {
                    order_data.push(obj);
                }
            });
            this.brandData = order_data;
        }
    }

    bulkAction(brandData: any) {
        let cart_data = [];
        this.brandData.forEach(obj => {
            console.log('obj');
            if (obj.state) {
                cart_data.push(obj.pk_order_id);
            }
        });

        this._orderService.updateStatus({ cart_id: cart_data, status: this.bulkActionData }).subscribe(
            data => {
                console.log('data');
                console.log(data);
                if (Number(data) && data != 0) {
                    this.toastr.success('Changes updated successfully!', 'Success Message');
                    this.getAllOrderList();
                } else {
                    this.toastr.error('Sorry! changes could not updated.', 'Error Message');
                }
            }
        );
    }


    public orderFilterAll(order_list: any) {
        let currentUser = JSON.parse(localStorage.getItem('currentUser'));
        let permission = currentUser.success.permission;
        let where: any;
        let loggedUserId = JSON.parse(localStorage.getItem('currentUser'));
        this.route.params.subscribe(
            parameter => {
                localStorage.setItem('section', 'order');
                loggedUserId = JSON.parse(localStorage.getItem('currentUser'));
                if (parameter.vendor_id) {
                    //console.log('parameter.vendor_id', parameter.vendor_id);
                    where = { loggedUserId: parameter.vendor_id, loggedUserRole: loggedUserId.success.role, role: '' };
                    this._userService.getById(parameter.vendor_id).subscribe(
                        data => {
                            this.pageTilte = data.success.store_name + '\'s Order';
                            this.back_btn = true;
                        }
                    );
                } else {
                    where = { loggedUserId: loggedUserId.success.id, loggedUserRole: loggedUserId.success.role };
                }
                this._orderService.getAll(where).subscribe(
                    data => {
                        this.brandData = data.success;
                        this.statusOrderData('completed');
                        this.statusOrderData('processing');
                        this.statusOrderData('on_hold');
                        this.statusOrderData('pending');
                        this.statusOrderData('cancelled');
                        this.statusOrderData('refunded');
                        console.log('all is well');
                    },
                    (err: string) => {
                        console.log('Sorry We have not found any product');

                    }
                );
            }
        );
    }

    getAllOrderList() {
        let currentUser = JSON.parse(localStorage.getItem('currentUser'));
        let permission = currentUser.success.permission;
        let where: any;
        let loggedUserId = JSON.parse(localStorage.getItem('currentUser'));
        this.route.params.subscribe(
            parameter => {
                localStorage.setItem('section', 'order');
                loggedUserId = JSON.parse(localStorage.getItem('currentUser'));
                if (parameter.vendor_id) {
                    //console.log('parameter.vendor_id', parameter.vendor_id);
                    where = { loggedUserId: parameter.vendor_id, loggedUserRole: loggedUserId.success.role, role: '' };
                    this._userService.getById(parameter.vendor_id).subscribe(
                        data => {
                            this.pageTilte = data.success.store_name + '\'s Order';
                            this.back_btn = true;
                        }
                    );
                } else {
                    where = { loggedUserId: loggedUserId.success.id, loggedUserRole: loggedUserId.success.role };
                }
                this._orderService.getAll(where).subscribe(
                    data => {
                        this.brandData = data.success;
                        this.filterDataHolder = undefined;
                        this.statusOrderData('completed');
                        this.statusOrderData('processing');
                        this.statusOrderData('on_hold');
                        this.statusOrderData('pending');
                        this.statusOrderData('cancelled');
                        this.statusOrderData('refunded');
                    },
                    (err: string) => {
                        console.log('Sorry We have not found any product');

                    }
                );
            }
        );
    }

}
